/* LANDING */

#landing {
  background-color: #f4f1ea;
  height: 90vh;
  display: flex;
  justify-content: space-around;
  overflow: hidden;
}

#landing h1 {
  font-size: 40px;
  animation: UpwardsFadeIn 1.5s .5s ease;
  animation-fill-mode: backwards;
}

#landing p {
  animation: UpwardsFadeIn 1.5s 1.5s ease;
  animation-fill-mode: backwards;
}

#landing a {
  animation: UpwardsFadeIn 1.5s 1.5s ease;
  animation-fill-mode: backwards;
}

#landing-content {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  margin-left: 60px;
}

#heroSectionMockups {
  height: 700px;
  align-self: flex-end;
  position: relative;
  top: 32px;
  animation: UpwardsFadeIn 1.5s 2s ease;
  animation-fill-mode: backwards;
}

.heroDisclaimer {
  font-size: 10px;
  font-style: italic;
  position: absolute;
  bottom: 5px;
  left: 90px;
}

#landing picture {
  position: relative;
}

.liveDemoButton {
  display: none;
  background-color: #e5c7ab;
  color: #fefefe;
  padding: 15px 20px;
  font-size: 12px;
  font-weight: 800;
  border-radius: 25px;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 12px;
}

/* FEATURES */

#features {
  background-color: #d0c6ba;
  padding: 60px;
  text-align: center;
  border-radius: 0 0 25px 25px;
}

.feature-container {
  display: flex;
  justify-content: space-around;
  margin-top: 60px;
}

.feature-icon-container {
  background-color: #f4f1ea;
  width: 80px;
  height: 80px;
  border-radius: 150px;
  padding: 35px;
  margin-bottom: 20px;
  display: flex;
  align-items: center;
}

.feature-icon {
  width: 100%;
}

.feature {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 250px;
  align-self: flex-start;
}

/* MOCKUPS */

#mockups {
  background-color: #f4f1ea;
  padding: 60px;
  height: 90vh;
  position: relative;
  display: flex;
  justify-content: space-around;
}

.mockupImage {
  height: 90vh;
  margin: 0 30px;
}

.mockupDisclaimer {
  font-size: 10px;
  font-style: italic;
  text-align: center;
  position: absolute;
  bottom: 15px;
}

.timer-container {
  display: none;
}

/* HOW IT WORKS */

#howItWorks {
  background-color: #f4f1ea;
  padding: 60px;
  padding-bottom: 0;
  text-align: center;
}

.tab-container {
  display: none;
}

.steps-container {
  display: flex;
  justify-content: center;
}

.step {
  width: 300px;
  margin: 60px 20px;
}

.step-image {
  width: 100%;
  border-radius: 25px 25px 0 0;
  margin: 0;
}

.step-description {
  background-color: #f5f5f5;
  border-radius: 15px 15px 25px 25px;
  position: relative;
  bottom: 15px;
  padding: 10px;
  text-align: start;
}

.step-label {
  background-color: #b6af86;
  color: #fff;
  font-weight: 600;
  padding: 10px 10px;
  width: 150px;
  margin: 0 auto;
  border: none;
  border-radius: 25px;
  margin-top: 10px;
}

/* TESTIMONIALS */

#testimonials {
  background-color: #f4f1ea;
  padding: 60px;
  padding-top: 0.1px;
  text-align: center;
}

.testimonial-container {
  display: flex;
  justify-content: space-evenly;
}

.testimonial {
  width: 350px;
  margin: 60px 10px;
  background-color: #fff;
  padding: 30px;
  border-radius: 15px;
}

.testimonial-image {
  width: 80px;
  border-radius: 50px;
}

/* SOCIAL MEDIA */

#socials {
  background-color: #e5c7ab;
  padding: 60px;
  border-radius: 25px 25px 0 0;
}

.socialMedia-container {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}

.socialMedia-icon {
  border: 1px solid #2d2926;
  padding: 10px;
  border-radius: 235px;
  overflow: visible;
  margin-right: 10px;
}

.socialMedia-icon:hover {
  border: 1px solid #7e7878;
}

.facebook-icon, .facebook-icon:hover {
  border: none;
}

.pricingLink {
  display: block;
  margin-top: 40px;
  background-color: #b6af86;
  color: #fff;
  padding: 10px 15px;
  width: 111px;
  text-align: center;
  font-weight: 500;
  text-transform: uppercase;
  transition: transform 0.3s ease;
}

.pricingLink:hover {
  color: #fff;
  transform: scale(1.1);
}

/* SMALLER SCREENS */

@media screen and (max-width: 1200px) {
  /* MOCKUPS */

  .mockups-container {
    display: none;
  }

  .timer-container {
    display: block;
  }

  .mockupImage {
    height: 90vh;
    opacity: 0;
  transition: opacity 1s;
  }

  .fade-in {
  opacity: 1;
  }

  /* TESTIMONIALS */

  .testimonial-container {
    flex-direction: column;
    align-items: center;
  }

  .testimonial {
    margin: 20px;
  }

}

@media screen and (max-width: 992px) {
  /* LANDING */

  #landing {
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 60px 0;
    height: auto;
    text-align: center;
  }

  #landing-content {
    align-items: center;
    margin: 0;
  }

  #heroSectionMockups {
    position: relative;
    top: 65px;
    height: 500px;
  }

  .heroDisclaimer {
    position: static;
    margin-bottom: -50px;
  }

  /* FEATURES */

  .feature-container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr;
    margin: 30px auto;
    align-items: center;
    justify-content: center;
    padding: 0;
  }

  .feature {
    margin: 20px auto;
  }

  /* HOW IT WORKS */

  .steps-container {
    flex-direction: column;
    align-items: center;
  }

  .step {
    margin: 20px;
  }

  /* Tabs */

  .steps-container {
    display: none;
  }

  .tab-container {
    display: block;
  }

  .tab {
    opacity: 0;
  transition: opacity 1s ease;
  height: 0; 
  overflow: hidden;
  }

  .active {
    display: flex;
    justify-content: center;
    opacity: 1;
  height: auto; 
  transition: opacity 1s ease;
  }

  .tab-button {
    background-color: #c6c6c6;
    color: #ffffff;
    padding: 5px 10px;
    margin: 5px;
    border: none;
    border-radius: 25px;
    transition: background-color 1s ease;
  }

  .activeButton {
    background-color: #b6af86;
    color: #fff;
    font-weight: 800;
  }

}

@media screen and (max-width: 576px) {

  /* LANDING */

  #heroSectionMockups {
    height: 400px;
  }

  .liveDemoButton {
    display: inline;
    position: absolute;
    bottom: 50px;
    right: 50px;
  }

  /* FEATURES */

  .feature-icon-container {
    width: 50px;
    height: 50px;
  }

  .feature-container {
    display: block;
  }

  /* MOCKUPS */

  #mockups {
    padding: 30px 10px;
  }

  .mockupImage {
    margin: 0;
  }

  .mockupDisclaimer {
    bottom: -40px;
  }

  /* TESTIMONIALS */

  .testimonial {
    padding: 5px;
    margin: 10px;
  }

  .testimonial-image {
    width: 60px;
    margin-top: 10px;
  }

}

/* ANIMATIONS */

@keyframes UpwardsFadeIn {
  0% {
    transform: translateY(5px);
    opacity: 0;
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}