@import url(https://fonts.googleapis.com/css2?family=Red+Hat+Display:ital,wght@0,300..900;1,300..900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* LANDING */

#landing {
  background-color: #f4f1ea;
  height: 90vh;
  display: flex;
  justify-content: space-around;
  overflow: hidden;
}

#landing h1 {
  font-size: 40px;
  animation: UpwardsFadeIn 1.5s .5s ease;
  animation-fill-mode: backwards;
}

#landing p {
  animation: UpwardsFadeIn 1.5s 1.5s ease;
  animation-fill-mode: backwards;
}

#landing a {
  animation: UpwardsFadeIn 1.5s 1.5s ease;
  animation-fill-mode: backwards;
}

#landing-content {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  margin-left: 60px;
}

#heroSectionMockups {
  height: 700px;
  align-self: flex-end;
  position: relative;
  top: 32px;
  animation: UpwardsFadeIn 1.5s 2s ease;
  animation-fill-mode: backwards;
}

.heroDisclaimer {
  font-size: 10px;
  font-style: italic;
  position: absolute;
  bottom: 5px;
  left: 90px;
}

#landing picture {
  position: relative;
}

.liveDemoButton {
  display: none;
  background-color: #e5c7ab;
  color: #fefefe;
  padding: 15px 20px;
  font-size: 12px;
  font-weight: 800;
  border-radius: 25px;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 12px;
}

/* FEATURES */

#features {
  background-color: #d0c6ba;
  padding: 60px;
  text-align: center;
  border-radius: 0 0 25px 25px;
}

.feature-container {
  display: flex;
  justify-content: space-around;
  margin-top: 60px;
}

.feature-icon-container {
  background-color: #f4f1ea;
  width: 80px;
  height: 80px;
  border-radius: 150px;
  padding: 35px;
  margin-bottom: 20px;
  display: flex;
  align-items: center;
}

.feature-icon {
  width: 100%;
}

.feature {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 250px;
  align-self: flex-start;
}

/* MOCKUPS */

#mockups {
  background-color: #f4f1ea;
  padding: 60px;
  height: 90vh;
  position: relative;
  display: flex;
  justify-content: space-around;
}

.mockupImage {
  height: 90vh;
  margin: 0 30px;
}

.mockupDisclaimer {
  font-size: 10px;
  font-style: italic;
  text-align: center;
  position: absolute;
  bottom: 15px;
}

.timer-container {
  display: none;
}

/* HOW IT WORKS */

#howItWorks {
  background-color: #f4f1ea;
  padding: 60px;
  padding-bottom: 0;
  text-align: center;
}

.tab-container {
  display: none;
}

.steps-container {
  display: flex;
  justify-content: center;
}

.step {
  width: 300px;
  margin: 60px 20px;
}

.step-image {
  width: 100%;
  border-radius: 25px 25px 0 0;
  margin: 0;
}

.step-description {
  background-color: #f5f5f5;
  border-radius: 15px 15px 25px 25px;
  position: relative;
  bottom: 15px;
  padding: 10px;
  text-align: start;
}

.step-label {
  background-color: #b6af86;
  color: #fff;
  font-weight: 600;
  padding: 10px 10px;
  width: 150px;
  margin: 0 auto;
  border: none;
  border-radius: 25px;
  margin-top: 10px;
}

/* TESTIMONIALS */

#testimonials {
  background-color: #f4f1ea;
  padding: 60px;
  padding-top: 0.1px;
  text-align: center;
}

.testimonial-container {
  display: flex;
  justify-content: space-evenly;
}

.testimonial {
  width: 350px;
  margin: 60px 10px;
  background-color: #fff;
  padding: 30px;
  border-radius: 15px;
}

.testimonial-image {
  width: 80px;
  border-radius: 50px;
}

/* SOCIAL MEDIA */

#socials {
  background-color: #e5c7ab;
  padding: 60px;
  border-radius: 25px 25px 0 0;
}

.socialMedia-container {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}

.socialMedia-icon {
  border: 1px solid #2d2926;
  padding: 10px;
  border-radius: 235px;
  overflow: visible;
  margin-right: 10px;
}

.socialMedia-icon:hover {
  border: 1px solid #7e7878;
}

.facebook-icon, .facebook-icon:hover {
  border: none;
}

.pricingLink {
  display: block;
  margin-top: 40px;
  background-color: #b6af86;
  color: #fff;
  padding: 10px 15px;
  width: 111px;
  text-align: center;
  font-weight: 500;
  text-transform: uppercase;
  transition: transform 0.3s ease;
}

.pricingLink:hover {
  color: #fff;
  transform: scale(1.1);
}

/* SMALLER SCREENS */

@media screen and (max-width: 1200px) {
  /* MOCKUPS */

  .mockups-container {
    display: none;
  }

  .timer-container {
    display: block;
  }

  .mockupImage {
    height: 90vh;
    opacity: 0;
  transition: opacity 1s;
  }

  .fade-in {
  opacity: 1;
  }

  /* TESTIMONIALS */

  .testimonial-container {
    flex-direction: column;
    align-items: center;
  }

  .testimonial {
    margin: 20px;
  }

}

@media screen and (max-width: 992px) {
  /* LANDING */

  #landing {
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 60px 0;
    height: auto;
    text-align: center;
  }

  #landing-content {
    align-items: center;
    margin: 0;
  }

  #heroSectionMockups {
    position: relative;
    top: 65px;
    height: 500px;
  }

  .heroDisclaimer {
    position: static;
    margin-bottom: -50px;
  }

  /* FEATURES */

  .feature-container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr;
    margin: 30px auto;
    align-items: center;
    justify-content: center;
    padding: 0;
  }

  .feature {
    margin: 20px auto;
  }

  /* HOW IT WORKS */

  .steps-container {
    flex-direction: column;
    align-items: center;
  }

  .step {
    margin: 20px;
  }

  /* Tabs */

  .steps-container {
    display: none;
  }

  .tab-container {
    display: block;
  }

  .tab {
    opacity: 0;
  transition: opacity 1s ease;
  height: 0; 
  overflow: hidden;
  }

  .active {
    display: flex;
    justify-content: center;
    opacity: 1;
  height: auto; 
  transition: opacity 1s ease;
  }

  .tab-button {
    background-color: #c6c6c6;
    color: #ffffff;
    padding: 5px 10px;
    margin: 5px;
    border: none;
    border-radius: 25px;
    transition: background-color 1s ease;
  }

  .activeButton {
    background-color: #b6af86;
    color: #fff;
    font-weight: 800;
  }

}

@media screen and (max-width: 576px) {

  /* LANDING */

  #heroSectionMockups {
    height: 400px;
  }

  .liveDemoButton {
    display: inline;
    position: absolute;
    bottom: 50px;
    right: 50px;
  }

  /* FEATURES */

  .feature-icon-container {
    width: 50px;
    height: 50px;
  }

  .feature-container {
    display: block;
  }

  /* MOCKUPS */

  #mockups {
    padding: 30px 10px;
  }

  .mockupImage {
    margin: 0;
  }

  .mockupDisclaimer {
    bottom: -40px;
  }

  /* TESTIMONIALS */

  .testimonial {
    padding: 5px;
    margin: 10px;
  }

  .testimonial-image {
    width: 60px;
    margin-top: 10px;
  }

}

/* ANIMATIONS */

@keyframes UpwardsFadeIn {
  0% {
    transform: translateY(5px);
    opacity: 0;
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}
/* GENERAL */

.About {
  background-color: #f4f1ea;
  padding: 100px 0;
  overflow-x: hidden;
}

.About h1 {
  margin: 15px 0;
}

.UpwardSpanOne {
  animation: SpanFadeIn 1s .5s ease;
  animation-fill-mode: backwards;
}

.UpwardSpanTwo {
  animation: SpanFadeIn 1s .8s ease;
  animation-fill-mode: backwards;
}

.UpwardSpanThree {
  animation: SpanFadeIn 1s 1.1s ease;
  animation-fill-mode: backwards;
}

.UpwardSpanFour {
  animation: SpanFadeIn 1s 1.4s ease;
  animation-fill-mode: backwards;
}

.About p {
  font-size: 16px;
}

.aboutPage-top {
  display: flex;
  align-items: center;
}

.aboutPage-bottom {
  display: flex;
  justify-content: right;
}

.decorative-block {
  background-color: #d0c6ba;
  color: #fff;
  padding: 60px;
}

.block-one {
  border-radius: 0px 250px 250px 0px;
  padding-right: 150px;
  max-width: 500px;
  margin-bottom: 60px;
  animation: MoveInRight 1s 2s ease;
  animation-fill-mode: backwards;
}

.block-two {
  border-radius: 250px 0px 0px 250px;
  padding-left: 250px;
  margin-left: 40px;
  animation: MoveInLeft 1s 2s ease;
  animation-fill-mode: backwards;
}

.block-two ul {
  padding: 0 20px;
}

.block-two li {
  margin: 5px 0;
}

#hiThere {
  margin: 0 auto;
}

#wedifyLogoShort {
  width: 280px;
  animation: LogoFadeIn .8s 1.5s ease-in-out;
  animation-fill-mode: backwards;
}

/* SMALLER SCREENS */

@media screen and (max-width: 992px) {
  

  .About p {
    font-size: 14px;
  }
  .aboutPage-top {
    flex-direction: column-reverse;
  }

  .block-one {
    align-self: flex-start;
    padding: 40px 100px 40px 20px;
  }

  .block-two {
    padding: 20px 15px 30px 110px;
    margin: 0;
    text-align: justify;
  }

  .block-two ul {
    font-size: 12px;
  }

  .block-two h1 {
    text-align: center;
  }

  #hiThere {
    margin-bottom: 80px;
  }

  #wedifyLogoShort {
    width: 235px;
  }

}

@keyframes SpanFadeIn {
  0% {
    transform: translateY(20px);
    opacity: 0;
  }
  100% {
    transform: translateY(0px);
    opacity: 1;
  }
}

@keyframes LogoFadeIn {
  0% {
    transform: translateY(50px);
    opacity: 0;
  }
  50% {
    transform: translateY(-20px);
  }
  100% {
    transform: translateY(0px);
  }
}

@keyframes MoveInRight {
  0% {
    transform: translateX(-1000px);
    opacity: 0;
  }
  100% {
    transform: translateX(0px);
    opacity: 1;
  }
}

@keyframes MoveInLeft {
  0% {
    transform: translateX(1000px);
    opacity: 0;
  }
  100% {
    transform: translateX(0px);
    opacity: 1;
  }
}
/* GENERAL */

.Pricing {
  background-color: #f4f1ea;
  display: flex;
  padding: 40px 100px;
}

.Pricing h1 {
  margin-bottom: 30px;
}

#fixedRatePromise {
  max-width: 600px;
}

/* PRICING BREAKDOWN */

.pricing-breakdown {
  text-align: center;
  background-color: #fff;
  border-radius: 10px;
  padding: 30px 80px;
  margin-right: 60px;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 300px;
  animation: MoveInRight .8s .5s ease;
  animation-fill-mode: backwards;
}

.currency {
  font: 800 20px "PT Serif", sans-serif;
  position: relative;
  bottom: 6.5px;
}

.price {
  font-size: 30px;
  font-weight: 800;
}

#onceOff {
  letter-spacing: 2px;
  font-weight: 600;
  font-size: 10px;
  position: relative;
  bottom: 10px;
}

.pricing-breakdown-container {
  text-align: start;
}

.pricing-breakdown-item {
  background-color: #e1e1e1;
  padding: 10px 15px 15px;
  font-size: 12px;
  font-weight: 600;
  border-radius: 5px;
}

.pricing-breakdown-icon {
  position: relative;
  top: 5px;
  margin-right: 5px;
}

.pricing-disclaimer {
  font-size: 10px;
  letter-spacing: 1px;
  font-style: italic;
  width: 220px;
  margin-top: 40px;
}

/* PRICING CONTENT */

.moveInTop {
  animation: DownwardFadeIn .8s 1.3s ease;
  animation-fill-mode: backwards;
}

#costsDisclaimer {
  margin-top: 20px;
  font-size: 12px;
  font-style: italic;
}

.pricing-summary {
  background-color: #fff;
  border-radius: 10px;
  padding: 20px;
  padding-bottom: 200px;
  text-align: left;
  position: relative;
  margin-top: 60px;
  animation: MoveInLeft .8s 1s ease;
  animation-fill-mode: backwards;
}

.pricing-summary h6 {
  font-weight: 400;
}

.pricing-summary hr {
  height: 0px;
  border: 0.5px solid #e1e1e1;
}

.summary-item {
  display: flex;
  justify-content: space-between;
}

.item-name, .item-price {
  font-weight: 800;
}

.item-description  {
  font-size: 10px;
  font-weight: 100;
  margin-top: -5px;
}

.payfastLogo {
  width: 100px;
  position: absolute;
  right: 10px;
  bottom: 100px;
}

.summary-total {
  background-color: #f5f5f5;
  border-radius: 10px;
  position: absolute;
  bottom: 0px;
  left: 0;
  width: 100%;
}

#total {
  margin: 20px;
}

.total-amount {
  font-weight: 800;
  font-size: 18px;
}

.payNowButton {
  position: absolute;
  right: 20px;
  bottom: 35%;
  background-color: #b6af86;
  color: #fff;
  font-size: 16px;
  font-weight: 800;
  padding: 5px 10px;
  border: transparent;
  border-radius: 5px;
}

.payNowButton:hover {
  cursor: pointer;
}

.payNowButton:active {
  background-color: #868060;
}

/* SMALLER SCREENS */

@media screen and (max-width: 992px) {
  
  .Pricing {
    flex-direction: column;
    align-items: center;
    text-align: center;
    padding: 20px;
  }

  .pricing-breakdown {
    margin: 40px 0;
    padding: 20px;
  }

  .moveInTop {
    animation: MoveInLeft .8s .5s ease;
    animation-fill-mode: backwards;
  }

}


/* GENERAL */

.Projects {
  background-color: #f4f1ea;
  text-align: center;
  padding: 40px 40px 0; 
  position: relative; 
}

.Projects h1 {
  margin-bottom: 40px;
  animation: DownwardFadeIn 1.5s .5s ease;
  animation-fill-mode: backwards;
}

/* MOCKUPS */

.projects-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.project-container {
  background-color: #d0c6ba;
  padding: 0 30px;
  width: 300px;
  height: 570px;
  border-radius: 250px 250px 0 0;
  overflow-y: scroll;
  scrollbar-width: none; 
  -ms-overflow-style: none;
  position: relative;
}

.project-container::-webkit-scrollbar {
  display: none;
}

.projects-container-one {
  animation: MoveInUpwards 1.5s .5s ease;
  animation-fill-mode: backwards;
}

.projects-container-two {
  animation: MoveInUpwards 1.5s 1s ease;
  animation-fill-mode: backwards;
}

.projects-container-three {
  animation: MoveInUpwards 1.5s 1.5s ease;
  animation-fill-mode: backwards;
}

.project-mockup {
  width: 200px;
  margin: 100px 0 30px;
}

.scroll-text {
  transform: rotate(90deg);
  font-size: 20px;
  text-transform: uppercase;
  letter-spacing: 1px;
  display: flex;
  align-items: center;
  animation: ScrollFadeIn 1.5s 3.5s ease;
  animation-fill-mode: backwards;
}

/* SMALLER SCREENS */

@media screen and (max-width: 1200px) {
  
  .projects-container-three {
    display: none;
  }

  .scroll-text-two {
    display: none;
  }

}

@media screen and (max-width: 992px) {

  .projects-content {
    justify-content: center;
  }

  .project-container {
    padding: 0 10px;
  }

  .projects-container-two {
    display: none;
  }

  .scroll-text {
    position :absolute;
    right: -20px;
    font-size: 14px;
  }

}

@keyframes MoveInUpwards {
  0% {
    transform: translateY(1000px);
  }
  100% {
    transform: translateY(0px);
  }
}

@keyframes ScrollFadeIn {
  0% {
    opacity: 0;
    
  }
  100% {
    opacity: 1;
  }
}
/* GENERAL */

