/* GENERAL */

.Projects {
  background-color: #f4f1ea;
  text-align: center;
  padding: 40px 40px 0; 
  position: relative; 
}

.Projects h1 {
  margin-bottom: 40px;
  animation: DownwardFadeIn 1.5s .5s ease;
  animation-fill-mode: backwards;
}

/* MOCKUPS */

.projects-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.project-container {
  background-color: #d0c6ba;
  padding: 0 30px;
  width: 300px;
  height: 570px;
  border-radius: 250px 250px 0 0;
  overflow-y: scroll;
  scrollbar-width: none; 
  -ms-overflow-style: none;
  position: relative;
}

.project-container::-webkit-scrollbar {
  display: none;
}

.projects-container-one {
  animation: MoveInUpwards 1.5s .5s ease;
  animation-fill-mode: backwards;
}

.projects-container-two {
  animation: MoveInUpwards 1.5s 1s ease;
  animation-fill-mode: backwards;
}

.projects-container-three {
  animation: MoveInUpwards 1.5s 1.5s ease;
  animation-fill-mode: backwards;
}

.project-mockup {
  width: 200px;
  margin: 100px 0 30px;
}

.scroll-text {
  transform: rotate(90deg);
  font-size: 20px;
  text-transform: uppercase;
  letter-spacing: 1px;
  display: flex;
  align-items: center;
  animation: ScrollFadeIn 1.5s 3.5s ease;
  animation-fill-mode: backwards;
}

/* SMALLER SCREENS */

@media screen and (max-width: 1200px) {
  
  .projects-container-three {
    display: none;
  }

  .scroll-text-two {
    display: none;
  }

}

@media screen and (max-width: 992px) {

  .projects-content {
    justify-content: center;
  }

  .project-container {
    padding: 0 10px;
  }

  .projects-container-two {
    display: none;
  }

  .scroll-text {
    position :absolute;
    right: -20px;
    font-size: 14px;
  }

}

@keyframes MoveInUpwards {
  0% {
    transform: translateY(1000px);
  }
  100% {
    transform: translateY(0px);
  }
}

@keyframes ScrollFadeIn {
  0% {
    opacity: 0;
    
  }
  100% {
    opacity: 1;
  }
}