/* GENERAL */

.About {
  background-color: #f4f1ea;
  padding: 100px 0;
  overflow-x: hidden;
}

.About h1 {
  margin: 15px 0;
}

.UpwardSpanOne {
  animation: SpanFadeIn 1s .5s ease;
  animation-fill-mode: backwards;
}

.UpwardSpanTwo {
  animation: SpanFadeIn 1s .8s ease;
  animation-fill-mode: backwards;
}

.UpwardSpanThree {
  animation: SpanFadeIn 1s 1.1s ease;
  animation-fill-mode: backwards;
}

.UpwardSpanFour {
  animation: SpanFadeIn 1s 1.4s ease;
  animation-fill-mode: backwards;
}

.About p {
  font-size: 16px;
}

.aboutPage-top {
  display: flex;
  align-items: center;
}

.aboutPage-bottom {
  display: flex;
  justify-content: right;
}

.decorative-block {
  background-color: #d0c6ba;
  color: #fff;
  padding: 60px;
}

.block-one {
  border-radius: 0px 250px 250px 0px;
  padding-right: 150px;
  max-width: 500px;
  margin-bottom: 60px;
  animation: MoveInRight 1s 2s ease;
  animation-fill-mode: backwards;
}

.block-two {
  border-radius: 250px 0px 0px 250px;
  padding-left: 250px;
  margin-left: 40px;
  animation: MoveInLeft 1s 2s ease;
  animation-fill-mode: backwards;
}

.block-two ul {
  padding: 0 20px;
}

.block-two li {
  margin: 5px 0;
}

#hiThere {
  margin: 0 auto;
}

#wedifyLogoShort {
  width: 280px;
  animation: LogoFadeIn .8s 1.5s ease-in-out;
  animation-fill-mode: backwards;
}

/* SMALLER SCREENS */

@media screen and (max-width: 992px) {
  

  .About p {
    font-size: 14px;
  }
  .aboutPage-top {
    flex-direction: column-reverse;
  }

  .block-one {
    align-self: flex-start;
    padding: 40px 100px 40px 20px;
  }

  .block-two {
    padding: 20px 15px 30px 110px;
    margin: 0;
    text-align: justify;
  }

  .block-two ul {
    font-size: 12px;
  }

  .block-two h1 {
    text-align: center;
  }

  #hiThere {
    margin-bottom: 80px;
  }

  #wedifyLogoShort {
    width: 235px;
  }

}

@keyframes SpanFadeIn {
  0% {
    transform: translateY(20px);
    opacity: 0;
  }
  100% {
    transform: translateY(0px);
    opacity: 1;
  }
}

@keyframes LogoFadeIn {
  0% {
    transform: translateY(50px);
    opacity: 0;
  }
  50% {
    transform: translateY(-20px);
  }
  100% {
    transform: translateY(0px);
  }
}

@keyframes MoveInRight {
  0% {
    transform: translateX(-1000px);
    opacity: 0;
  }
  100% {
    transform: translateX(0px);
    opacity: 1;
  }
}

@keyframes MoveInLeft {
  0% {
    transform: translateX(1000px);
    opacity: 0;
  }
  100% {
    transform: translateX(0px);
    opacity: 1;
  }
}