/* GENERAL */

.App {
  background-color: #f4f1ea;
  margin-bottom: 100px;
  color: #2d2926;
  margin: 0;
}

/* Typography */

@import url('https://fonts.googleapis.com/css2?family=Red+Hat+Display:ital,wght@0,300..900;1,300..900&display=swap');

h1 {
  font: 400 30px "PT Serif", sans-serif;
  margin: 10px 0;
}

h6 {
  font: 800 20px "PT Serif", sans-serif;
  margin: 15px 0;
}

p {
  font: 14px "Red Hat Display";
  margin: 10px 0;
}

a {
  font: 14px "Red Hat Display";
  text-decoration: none;
  color: #2d2926;
}

a:hover {
  cursor: pointer;
  color: #7e7878;
}


/* General style for all button-type links */

.link-button {
  background: #b6af86;
  color: #fff;
  font-weight: 600;
  text-decoration: none;
  margin: 10px 0;
  padding: 10px 15px;
  border-radius: 6px;
  transition: color 0.3s ease, transform 0.3s ease;
}

.link-button:hover {
  transform: scale(1.1);
  text-decoration: none;
  color: #fff;
}

/* HEADER */

.App-header {
  background-color: #fff;
  display: flex;
  justify-content: space-between;
  padding: 10px 40px;
  position: sticky;
  top: 0px;
  z-index: 10;
  animation: DownwardFadeIn 1.5s 2.5s ease backwards;
}

.App-logo {
  height: 50px;
}

.navigation-links, .footer-navigation {
  display: flex;
  list-style-type: none;
}

.navigation-link {
  margin: 0 20px;
}

/* Navbar Toggler */

.navbar-toggler {
  display: none;
}

/* FOOTER */

.App-footer {
  font-size: 14px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 20px;
  z-index: 10;
  background-color: #fff;
}

.footer-logo {
  height: 30px;
}

.footer-navigation {
  padding: 0;
}

.footer-navigation-link {
 margin: 0 10px; 
}

.footer-copyright {
  font-size: 14px;
}

/* SMALLER SCREEN SIZES */

@media screen and (max-width: 768px) {

  /* HEADER */

  .navigation-links {
    display: none;
  }

  .navbar-toggler {
    background-color: transparent;
    display: flex;
    flex-direction: column;
    border: none;
    width: 50px;
    height: 50px;
    padding: 0px;
    align-items: center;
    justify-content: center;
  }

  .navbar-toggler:hover {
    cursor: pointer;
  }

  .toggler-line {
    width: 30px;
    height: 3px;
    filter: opacity(80%);
    transition: opacity 0.1s ease , transform 0.3s ease;
  }

  .toggler-line-one {
    position: relative;
    bottom: 7px;
  }

  .toggler-line-three {
    position: relative;
    top: 7px;
  }

  .navbar-menuOpen .toggler-line-one {
    transform: translateY(10px) rotate(45deg);
  }

  .navbar-menuOpen .toggler-line-two {
    opacity: 0;
  }

  .navbar-menuOpen .toggler-line-three {
    transform: translateY(-10px) rotate(-45deg);
  }

  /* Collapsed Navigation Menu */

  .navigation-collapsed {
    display: flex;
    position: absolute;
    flex-direction: column;
    list-style-type: none;
    width: 100%;
    left: 0;
    padding: 0;
    top: 55px;
    animation: menuEaseDown 0.5s ease;
  }

  .navigation-collapsed-link {
    background-color: #fff;
    width: 100%;
    text-align: center;
    padding: 10px 0;
  }

  @keyframes menuEaseDown {
    0% {opacity: 0;}
    100% {opacity: 100%;}
  }

}

@media screen and (max-width: 576px) {

  #landing h1 {
    font-size: 30px;
  }

  h1 {
    font-size: 25px;
  }

  h6 {
    font-size: 16px;
  }

  p {
    font-size: 14px;
  }

}

/* ANIMATIONS */

@keyframes DownwardFadeIn {
  0% {
    transform: translateY(-300px);
    opacity: 0;
    background-color: #f4f1ea;
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}
