/* GENERAL */

.Contact {
  background-color: #f4f1ea;
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 60px;
  padding-bottom: 0;
  overflow-x: hidden;
}

.Contact p {
  margin: 0;
}

label {
  font: 16px "Red Hat Display";
  padding-left: 10px;
}

input, textarea {
  font: 600 14px "Red Hat Display";
  color: #f5f5f5;
  display: block;
  width: 100%;
  background-color: transparent;
  border: 1px solid #f5f5f5;
  border-radius: 15px;
  padding: 3px;
  padding-left: 10px;
}

input {
  margin: 3px 0 10px;
}

textarea {
  margin-top: 5px;
  padding: 10px;
}

input:focus, textarea:focus {
  outline: 2px solid #f5f5f5;
}

/* CONTENT */

#contactText {
  margin: 30px 0;
  width: 400px;
}

.Contact .contactSubHeading {
  margin-top: 20px;
}

/* CONTACT FORM */

.contact-form {
  background-color: #d0c6ba;
  color: #f5f5f5;
  padding: 50px;
  border-radius: 250px 250px 0px 0px;
  height: 540px;
  animation: MoveInLeft 1.5s 2.2s ease backwards;
}

.contact-form h1 {
  margin: 30px 40px;
  text-align: center;
}

#formSubmitButton {
  font: 800 14px "Red Hat Display";
  background-color: #b6af86;
  color: #f5f5f5;
  border: none;
  border-radius: 25px;
  padding: 10px 20px;
  display: block;
  margin: 15px auto;
}

#formSubmissionMessage {
  font-size: 18px;
  font-weight: 400;
  text-align: center;
  width: 200px;
  margin: auto;
  position: relative;
  top: 100px;
  animation: FadeIn 1s ease;
}

/* Fade-In Animation */

@keyframes FadeIn {
  0% {
    opacity: 0;
    transform: translatey(10px);
  }
  100% {
    opacity: 1;
  }
}

/* SMALLER SCREENS */ 
  @media screen and (max-width: 992px) {
    
    .Contact {
      flex-direction: column;
      text-align: center;
    }

    .contact-form {
      margin-top: 80px;
      text-align: start;
      animation: UpwardsFadeIn 1s 2.2s ease backwards;
    }

  }
