/* GENERAL */

.Pricing {
  background-color: #f4f1ea;
  display: flex;
  padding: 40px 100px;
}

.Pricing h1 {
  margin-bottom: 30px;
}

#fixedRatePromise {
  max-width: 600px;
}

/* PRICING BREAKDOWN */

.pricing-breakdown {
  text-align: center;
  background-color: #fff;
  border-radius: 10px;
  padding: 30px 80px;
  margin-right: 60px;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 300px;
  animation: MoveInRight .8s .5s ease;
  animation-fill-mode: backwards;
}

.currency {
  font: 800 20px "PT Serif", sans-serif;
  position: relative;
  bottom: 6.5px;
}

.price {
  font-size: 30px;
  font-weight: 800;
}

#onceOff {
  letter-spacing: 2px;
  font-weight: 600;
  font-size: 10px;
  position: relative;
  bottom: 10px;
}

.pricing-breakdown-container {
  text-align: start;
}

.pricing-breakdown-item {
  background-color: #e1e1e1;
  padding: 10px 15px 15px;
  font-size: 12px;
  font-weight: 600;
  border-radius: 5px;
}

.pricing-breakdown-icon {
  position: relative;
  top: 5px;
  margin-right: 5px;
}

.pricing-disclaimer {
  font-size: 10px;
  letter-spacing: 1px;
  font-style: italic;
  width: 220px;
  margin-top: 40px;
}

/* PRICING CONTENT */

.moveInTop {
  animation: DownwardFadeIn .8s 1.3s ease;
  animation-fill-mode: backwards;
}

#costsDisclaimer {
  margin-top: 20px;
  font-size: 12px;
  font-style: italic;
}

.pricing-summary {
  background-color: #fff;
  border-radius: 10px;
  padding: 20px;
  padding-bottom: 200px;
  text-align: left;
  position: relative;
  margin-top: 60px;
  animation: MoveInLeft .8s 1s ease;
  animation-fill-mode: backwards;
}

.pricing-summary h6 {
  font-weight: 400;
}

.pricing-summary hr {
  height: 0px;
  border: 0.5px solid #e1e1e1;
}

.summary-item {
  display: flex;
  justify-content: space-between;
}

.item-name, .item-price {
  font-weight: 800;
}

.item-description  {
  font-size: 10px;
  font-weight: 100;
  margin-top: -5px;
}

.payfastLogo {
  width: 100px;
  position: absolute;
  right: 10px;
  bottom: 100px;
}

.summary-total {
  background-color: #f5f5f5;
  border-radius: 10px;
  position: absolute;
  bottom: 0px;
  left: 0;
  width: 100%;
}

#total {
  margin: 20px;
}

.total-amount {
  font-weight: 800;
  font-size: 18px;
}

.payNowButton {
  position: absolute;
  right: 20px;
  bottom: 35%;
  background-color: #b6af86;
  color: #fff;
  font-size: 16px;
  font-weight: 800;
  padding: 5px 10px;
  border: transparent;
  border-radius: 5px;
}

.payNowButton:hover {
  cursor: pointer;
}

.payNowButton:active {
  background-color: #868060;
}

/* SMALLER SCREENS */

@media screen and (max-width: 992px) {
  
  .Pricing {
    flex-direction: column;
    align-items: center;
    text-align: center;
    padding: 20px;
  }

  .pricing-breakdown {
    margin: 40px 0;
    padding: 20px;
  }

  .moveInTop {
    animation: MoveInLeft .8s .5s ease;
    animation-fill-mode: backwards;
  }

}

